<template>
    <div class="public_content clearfix">
        <div class="public_title"><span>商铺房源库</span></div>

        <el-button type="primary" class="public_add" @click="$router.push({name: 'addstore'})">录入房源</el-button>

        <!-- 刷选 -->
        <div class="clearfix"> 
            <el-tabs class='fl' v-model="updown" @tab-click="handleCurrentChange(1)">
                <el-tab-pane name="1">
                    <span slot="label">已上架 <el-badge class="mark" :value="total_1" :max="99" /></span>
                </el-tab-pane>
                <el-tab-pane name="0">
                    <span slot="label">未上架 <el-badge class="mark" :value="total_0" :max="99" /></span>
                </el-tab-pane>
            </el-tabs>
            <div class="fr">
                <el-select v-model="shopnature" @change="handleCurrentChange(1)" style="width: 100px;" placeholder="请选择">
                        <el-option value="" label="全部"></el-option>
                        <el-option value="0" label="出租"></el-option>
                        <el-option value="1" label="出售"></el-option>
                        <el-option value="2" label="转让"></el-option>
                    </el-select>

                <el-input style="width: 300px;"  placeholder="请输入标题/小区名称/房源编号" v-model="keywords" >
                    <el-button slot="append" icon="el-icon-search" @click="handleCurrentChange(1)"></el-button>
                </el-input> 

             </div>
        </div> 
        <ul v-show="page.total" class="lists_wrap">
            <el-checkbox-group v-model="checkList" @change="handleCheckedChange">
                
            <li v-for="(item,index) in lists" :key="index" class="item">
                
                <p>
                    <el-checkbox style="margin-right: 20px;" v-show="updown == 0" :label="item.id" :key="index">
                        编号{{item.sale_no}}
                    </el-checkbox>
                    <span v-show="updown == 1">编号{{item.sale_no}}</span>

                    <span class="time">刷新时间：{{item.refreshtime | formatDate}}</span>
                </p>

                <div class="detail">
                    <img class="thumb" :src="item.thumb" />

                    <span class="shopnature" :class="item.shopnature | filter">
                        {{item.shopnature == 0 ? '租' : item.shopnature == 1 ? '售' : '转'}}
                    </span>
                    <div class="info">
                        <p class="name">{{item.title}}</p>
                        <p>
                            <span class="avgprice">{{item.price ? item.price : '面议'}}
                                <span v-show="item.price" style='color: #333;font-weight: 400;font-size: 14px;'>元/月</span></span>
                            <span class="address">
                                 {{item.address}}
                            </span>
                        </p>
                    </div>

                    <div class="flag">
                        <!-- 状态过滤 -->
                        <img :src="item.buy_location | listImg" alt="" class="mark">

                    </div> 
                    
                    <span v-show="updown == 1" class="showStatus" :class="Number(item.status) ? 'orange' : 'green'">{{Number(item.status) ? '已审核' : '待审核'}}</span>
                    


                    <div class="status" style="font-size: 14px">
                        <span class="public_btn darkgreen" v-show="Number(item.updown)" @click="handle(item.id,1)"><i class="iconfont">&#xe65d;</i>刷新</span>
                        <span class="public_btn" :class="Number(item.updown) ? 'yellow' : 'darkgreen'" @click="handle(item.id,(item.updown ? 4 : 3))"><i class="iconfont">{{Number(item.updown) ? '&#xe63f;' : '&#xe63a;'}}</i>{{Number(item.updown) ? '下架' : '上架'}}</span>
                        <span class="public_btn blue" v-show="!Number(item.updown)"  @click="$router.push({name: 'addstore', query: {id: item.id}})"><i class="el-icon-edit-outline"></i>编辑</span>
                        <span class="public_btn orange" v-show="!Number(item.updown)" @click="handle(item.id,2)"><i class="iconfont">&#xe655;</i>删除</span>

                    </div>

                   
                    <div class="num" v-show="updown == 1">
                        <i class="iconfont">&#xe64f;</i>
                        <span>{{item.hits ? item.hits : 0}}</span>
                    </div>
                </div>
                 
            </li>
            </el-checkbox-group>

        </ul>


        <div class="noContent"  v-show="!page.total">暂无数据</div>

        <div class="inlineBlock fl">

        
            <el-checkbox v-model="checkAll" @change="handleCheckAllChange" v-show="page.total && updown == 0"></el-checkbox>
            <span class="public_btn orange small" v-show="page.total && updown == 0" @click="deleteAll"><i class="iconfont">&#xe655;</i>删除</span>
        </div>

        <!-- 分页 -->
        <div class="inlineBlock fr" v-show="page.total">

            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="page.pageNo"
                :page-sizes="[10, 20, 30, 40]"
                :page-size="page.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="page.total">
            </el-pagination> 
        </div>
    </div>
</template>

<script>
import { getSaleRentShopList, operateSaleRentShop } from "@/api/house";
export default {
  data() {
    return {
      page: {
        total: 0,
        pageNo: 1,
        pageSize: 10
      },
      total_1: 0,
      total_0: 0,
      keywords: "",
      shopnature: "",
      checkList: [], // 存储选中的选项
      filterlists: [],
      checkAll: false,
      updown: "1",
      searchInfo: "",
      lists: []
    };
  },
  created() {
    console.log(this.$store.state.app.storecondition)
    if (this.$store.state.app.storecondition.pageNo) {
      this.page.pageNo = this.$store.state.app.storecondition.pageNo;
      this.page.pageSize = this.$store.state.app.storecondition.pageSize;
      this.updown = this.$store.state.app.storecondition.updown;
      this.keywords = this.$store.state.app.storecondition.keywords;
    } 
    this.fetchData();
  },
  beforeRouteLeave(to, from, next) { 
    if (to.name == "addstore" && to.query.id) {
      this.$store.commit("SET_STORECONDITION", {
        pageNo: this.page.pageNo,
        pageSize: this.page.pageSize,
        select_type: 1,
        updown: this.updown,
        keywords: this.keywords
      });
    } else {
      this.$store.commit("SET_STORECONDITION", {});
    }

    next();
  },
  filters: {
    filter(val) {
      if (val == 0) {
        return "rent";
      } else if (val == 1) {
        return "sell";
      } else {
        return "transfer";
      }
    }
  },
  methods: {
    fetchData() {
      return new Promise((resolve, reject) => {
        let data = {
          pageNo: this.page.pageNo,
          pageSize: this.page.pageSize,
          select_type: 3,
          updown: this.updown,
          keywords: this.keywords,
          shopnature: this.shopnature
        };
        getSaleRentShopList(data)
          .then(res => {
            this.lists = res.data.data.data;
            if (res.data.data.total) {
              this.lists.forEach(item => {
                this.filterlists.push(item.id);
              });
            }
            this.total_0 = res.data.data.count.total_0;
            this.total_1 = res.data.data.count.total_1;
            this.page.total = res.data.data.total;

            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    handleCurrentChange(val) {
      this.checkList = [];
      this.checkAll = false;
      this.page.pageNo = val;
      this.fetchData();
    },

    /* 页数变化 */
    handleSizeChange(val) {
      this.page.pageSize = val;
      this.fetchData();
    },
    handleCheckAllChange(flag) {
      this.checkList = flag ? this.filterlists : [];
    },

    /* 批量删除 */
    deleteAll() {
      this.checkList.forEach(item => {
        this.handle(item, 2);
      });
    },
    handleCheckedChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.lists.length;
    },

    /* 操作 */
    handle(id, type) {
      return new Promise((resolve, reject) => {
        let data = {
          id: id,
          type: type,
          method: 3
        };
        operateSaleRentShop(data)
          .then(res => {
            if (res.data.code == 1) {
              this.$message.success("操作成功");
              this.fetchData().then(res => {
                if (res.data.data == "") {
                  this.page.pageNo--;
                  this.fetchData();
                }
              });
            }
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    }
  }
};
</script>

<style lang='scss' scoped>
.shopnature {
  position: absolute;
  left: 0;
  top: 10px;
  width: 21px;
  height: 18px;
  line-height: 18px;
  color: #fff;
  font-size: 12px;
  text-align: center;

  &.rent {
    background: #34bfa3;
  }
  &.rent:after {
    border-left-color: #34bfa3;
  }

  &.sell {
    background: #f44864;
  }
  &.sell:after {
    border-left-color: #f44864;
  }

  &.transfer {
    background: #f5c239;
  }
  &.transfer:after {
    border-left-color: #f5c239;
  }
  &:after {
    content: "";
    right: -9px;
    position: absolute;
    width: 0;
    height: 0;
    border: 9px solid;
    border-right-width: 0px;
    border-top-color: transparent;
    border-bottom-color: transparent;
  }
}
</style>



